import req from '@/http/http'

//删除会议
export const delMetting = data => {
  let path = '/meeting/delete'
  return req.post(path, data)
}
//根据用户查会议相关信息
export const getMeetByUser = data => {
  let path = 'meeting/selectInitiated'
  return req.post(path, data)
}

//未处理的会议
export const getWaitMeet = data => {
  let path = '/meeting/selectUntreated'
  return req.post(path, data)
}

//已经处理的会议
export const getAlreaeyMeet = data => {
  let path = '/meeting/selectProcessed'
  return req.post(path, data)
}

//获取会议详情
export const getMeetDet = data => {
  let path = '/meeting/selectOne'
  return req.post(path, data)
}

//获取会议列表
export const getMeetList = data => {
  let path = '/meeting/list'
  return req.post(path, data)
}



