<template>
  <div class="containerWrap">
    <el-container class="container">
      <el-main class="rightMain">
        <el-container>
          <el-header class="dzy-main-header">
            <div class="btn-group-el">
              <el-button type="primary" icon="el-icon-plus" @click="add"
                >新增</el-button
              >
              <el-button type="success" icon="el-icon-view" @click="get"
                >查看</el-button
              >
              <el-button type="danger" icon="el-icon-delete" @click="del"
                >删除</el-button
              >
              <el-button type="warning" icon="el-icon-edit" @click="edit"
                >修改</el-button
              >
            </div>
            <div class="dzy-custom-search-box">
              <div>
                组织名称 :
                <el-input
                  size="small"
                  placeholder="请输入姓名"
                  v-model="queryData.name"
                  clearable
                  class="dzy-custom-input"
                ></el-input>
              </div>
              <div>
                <el-button
                  size="small"
                  @click="searchHandleClick"
                  type="primary"
                  icon="el-icon-search"
                  >查询</el-button
                >
              </div>
            </div>
          </el-header>
          <el-main class="tableMain">
            <el-table
              ref="multipleTable"
              :data="tableData"
              style="width: 100%"
              :header-cell-style="{ backgroundColor: '#CF4B34', color: '#fff' }"
              :max-height="430"
              @select="selectChange"
              @row-click="rowClickBack"
              stripe
            >
              <el-table-column type="selection" width="55"></el-table-column>
              <el-table-column
                v-for="(item, index) in tableColumns"
                :key="index"
                :prop="item.prop"
                :width="item.width"
                :label="item.label"
              ></el-table-column>
            </el-table>
            <el-pagination
              background
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              layout="total, prev, pager, next, jumper"
              :total="tableData.total"
            ></el-pagination>
          </el-main>
        </el-container>
      </el-main>
    </el-container>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose"
    >
      <el-form
        :model="formData"
        :rules="rules"
        ref="formData"
        label-width="100px"
        size="small"
      >
        <el-divider content-position="left">基本信息</el-divider>
        <el-row :gutter="24">
          <el-col :span="8">
            <el-form-item label="党组织名称 :" prop="name">
              <el-input
                v-model="formData.orgName"
                placeholder="党组织名称"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="党组织类型 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="党组织类型"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="上级党组织 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="上级党组织"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="8">
            <el-form-item label="创建时间 :" prop="name">
              <el-input
                v-model="formData.orgName"
                placeholder="创建时间"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="最近修改时间 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="最近修改时间"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-divider content-position="left">位置信息</el-divider>
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="地址 :" prop="name">
              <el-input
                v-model="formData.orgName"
                placeholder="地址"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-divider content-position="left">组织信息</el-divider>
        <el-row :gutter="24">
          <el-col :span="8">
            <el-form-item label="书记姓名 :" prop="name">
              <el-input
                v-model="formData.orgName"
                placeholder="书记姓名"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="副书记姓名 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="副书记姓名"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="党组织联系人 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="党组织联系人"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="8">
            <el-form-item label="党组织电话 :" prop="name">
              <el-input
                v-model="formData.orgName"
                placeholder="党组织联系电话"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="组织委员 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="组织委员"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="宣传委员 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="宣传委员"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="8">
            <el-form-item label="纪检委员 :" prop="name">
              <el-input
                v-model="formData.orgName"
                placeholder="纪检委员"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="青年委员 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="青年委员"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="党员人数 :" prop="source">
              <el-input
                v-model="formData.source"
                placeholder="党员人数"
                disabled
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose" v-if="showCommit">取 消</el-button>
        <el-button type="primary" @click="commit" v-if="showCommit"
          >提 交</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import AppUpload from "@/components/AppAvatarUpload";
import {
  delPartyDept,
  addPartyDept,
  getPartyDeptDet,
  queryPartyDeptPage,
  getPartyDeptChild,
  updatePartyDeptInfo,
  updatePartyDeptStatus,
} from "@/http/dangwuApi";
import { getMeetList } from "@/http/tongzhiApi";
export default {
  data() {
    return {
      showCommit: true,
      dialogVisible: false,
      dialogTitle: "",
      pageSize: 10,
      currentPage: 1,
      queryData: {
        current: 1,
        size: 10,
        data: {},
      },
      formData: {},
      rules: {},
      tableColumns: [
        {
          prop: "meetingTile",
          label: "会议标题",
          width: "150px",
        },
        {
          prop: "meetingTile",
          label: "会议标题",
          width: "150px",
        },
        {
          prop: "meetingAddress",
          label: "会议地址",
          width: "",
        },
      ],
      multipleSelection: null,
      tableData: [],
    };
  },
  mounted() {
    this.getTableData();
  },
  methods: {
    handleCurrentChange(val) {
      console.log(val);
    },
    getTableData() {
      getMeetList(this.queryData).then((resp) => {
        let data = resp.value;
        if (data && data.code == "0000") {
          this.tableData = data.data.records;
        } else {
          this.$message.error(data.message);
        }
      });
    },
    //新增
    add() {
      this.dialogVisible = true;
      this.dialogTitle = "新增党组织";
      this.showCommit = true;
    },
    //查看
    get() {
      if (!this.multipleSelection) {
        this.$message.warning("请选择要查看的行");
      } else {
        let params = { orgId: this.multipleSelection.cloudOrg.id };
        getPartyDeptDet(params).then((resp) => {
          let data = resp.value;
          if (data && data.code == "0000") {
            this.formData = data.data;
            this.dialogTitle = "查看" + this.multipleSelection.cloudOrg.orgName;
            this.showCommit = false;
            this.dialogVisible = true;
          } else {
            this.$message.error(data.messsage);
          }
        });
      }
    },
    //编辑
    edit() {
      if (!this.multipleSelection) {
        this.$message.warning("请选择要修改的行");
      } else {
        let params = { orgId: this.multipleSelection.cloudOrg.id };
        getPartyDeptDet(params).then((resp) => {
          let data = resp.value;
          if (data && data.code == "0000") {
            this.formData = data.data;
            this.dialogTitle = "修改" + this.multipleSelection.cloudOrg.orgName;
            this.showCommit = true;
            this.dialogVisible = true;
          } else {
            this.$message.error(data.messsage);
          }
        });
      }
    },
    //提交
    commit() {
      if (this.formData.id) {
        updatePartyDeptInfo(this.formData).then((resp) => {
          let data = resp.value;
          if (data && data.code == "0000") {
            this.$message.success("修改成功");
            this.handleClose();
          } else {
            this.$message.error(data.message);
          }
        });
      } else {
        addPartyDept(this.formData).then((resp) => {
          let data = resp.value;
          if (data && data.code == "0000") {
            this.$message.success("新增成功");
            this.handleClose();
          } else {
            this.$message.error(data.message);
          }
        });
      }
    },
    //删除
    del() {
      if (!this.multipleSelection) {
        this.$message.warning("请选择要删除的行");
      } else {
        this.$confirm("确定删除该组织吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            delPartyDept().then((resp) => {
              let data = resp.value;
              if (data && data.code == "0000") {
                this.$message.success("删除成功!");
              } else {
                this.$message.error(data.message);
              }
            });
          })
          .catch(() => {
            this.$message.info("已取消删除!");
          });
      }
    },

    //点击表格行选中
    rowClickBack(row, column, event) {
      this.$refs.multipleTable.clearSelection();
      this.$refs.multipleTable.toggleRowSelection(row);
      this.multipleSelection = row;
    },
    //单选框变化
    selectChange(selection, row) {
      this.$refs.multipleTable.clearSelection();
      this.$refs.multipleTable.toggleRowSelection(row);
      this.multipleSelection = row;
    },
    //弹出框关闭之前
    handleClose() {
      this.formData = {};
      this.dialogVisible = false;
    },
    searchHandleClick() {
      this.get;
    },
  },
};
</script>
<style scoped>
.containerWrap {
  padding: 10px;
  height: 100%;
  box-sizing: border-box;
}
.container {
  height: 100%;
}
</style>



